.task {
  width: 100%;
  display: flex;
  justify-content: flex-end;
  margin: 10px 0;
  flex-direction: column;



  .task-inner {
    display: flex;
    justify-content: space-between;
    width: 100%;

    .task-priority {
      width: 5px;
      height: 100%;
      background-color: red;
      border-radius: 0;
    }

    img {
      width: 40px;
      height: 40px;
      min-width: 40px;
      min-height: 40px;

      background-color: #404040;
      border-radius: 50%;
      margin: 5px 5px 0 0;
    }

    &>div{
      display: flex;
      flex-direction: column;
      width: 100%;
      background: var(--tg-theme-secondary-bg-color, #000);
      border-top-right-radius: 15px;
      border-bottom-right-radius: 15px;
      position: relative;
      overflow: hidden;
      padding-left: 5px;
      box-sizing: border-box;

      p{
        height: auto;
        box-sizing: border-box;
        width: 100%;
        text-align: start;
        margin: 0;
        background: 0;
        padding: 10px 5px;
      }

      div{
        width: 100%;
        display: flex;
        flex-direction: row;
        box-sizing: border-box;
        padding: 10px 10px 0 0;
        justify-content: flex-start;

        p {
          padding: 0 5px 10px 5px;
          color: #4b4b4b;
          width: fit-content;
        }
        .task-check-box {
          position: absolute;
          display: unset;
          top: -10px;
          right: -10px;
          box-sizing: border-box;
          min-width: 40px;
          min-height: 40px;
          width: 40px;
          height: 40px;
          background: #ff3427;
          padding: 0;
          border-left: 0;
          border-radius: 40%;
        }
      }
    }
  }

  .task-buttons {
    margin-top: 10px;
    display: flex;
    width: 100%;
    justify-content: space-evenly;

    button {
      clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
      color: white;
      border: 0;
      border-radius: 15px;
      height: 25px;
      padding: 0;
      box-sizing: border-box;
      flex-basis: 25%;

      &:first-child {
        border: #79C063 1px solid;
        background: var(--tg-theme-bg-secondary-color, #000);
      }

      &:nth-child(2) {
        border: #D7B258 1px solid;
        background: var(--tg-theme-bg-secondary-color, #000);

      }

      &:last-child {
        border: #E57770 1px solid;
        background: var(--tg-theme-bg-secondary-color, #000);

      }
    }
  }
}